                '참새 100마리도 너끈히 물리쳤던',
                '아버지가 쓰러지셨다고?',
            ],
            answer_1 : {
                text:[
                    '모든 것을 뒤로하고',
                    '왕국으로 달려간다.',
                ],
                theme : "S"
            },
            answer_2 : {
                text : [
                    '헉!아버지가??안돼!!',
                    '(아버지와 지낸 시간을 떠올리며 슬퍼한다)',
                ],
                theme : "N"
            }
        },
        {
            gaze:2,
            level : 1,
            sentence:[
                '혼자 가기는 힘들 테니 콩(시중)과',
                '함께 다녀오거라.',
            ],
            answer_1 : {
                text:[
                    '콩과 함께 가라고?',
                    '나 혼자 가는 게 편하단 말이지.',
                ],
                theme : "I"
            },
            answer_2 : {
                text : [
                    '전국을 돌아본다니!',
                    '정말 기대된다! 콩 너도 설레지?',
                ],
                theme : "E"
            }
        },
        {
            gaze:3,
            level : 2,
            sentence:[
                '노잣돈이 달랑 이것뿐이야?',
                '그럼 떠나기전 잠시 놀아볼까!',
            ],
            answer_1 : {
                text:[
                    '돈은 이 정도만 쓰자.',
                    '일정에 맞게 계획적으로 사용해야지.',
                ],
                theme : "J"
            },
            answer_2 : {
                text : [
                    '에라 모르겠다. 질러!',
                    '다 쓰고 모자라면 또 달라고 하자.',
                ],
                theme : "P"
            }
        },
        {
            gaze:4,
            level : 3,
            sentence:[
                '다리도 뻗기 힘든 단칸방에서',
                '다 같이 자야하다니..',
            ],
            answer_1 : {
                text:[
                    '나는 여기서 잘 테니',
                    '너는 저기서 자도록 해',
                ],
                theme : "T"
            },
            answer_2 : {
                text : [
                    '그래, 내가 언제 이런 곳에서 자보겠어.',
                    '이것도 다 낭만이지.',
                ],
                theme : "F"
            }
        },
        {
            gaze:5,
            level : 4,
            sentence:[
                '이튿날 아침, 노부부가',
                '한상 가득 아침을 차려주셨다.',
            ],
            answer_1 : {
                text:[
                    '밥과 국, 반찬 등',
                    '6첩 반상이네.',
                ],
                theme : "S"
            },
            answer_2 : {
                text : [
                    '우리에게 이렇게나 대접해 주시다니..',
                    '정말 감사하다.',
                ],
                theme : "N"
            }
        },
        {
            gaze:6,
            level : 5,
            sentence:[
                '채비를 하고 떠나려고하니',
                '발걸음을 떨어지질 않는다.',
            ],
            answer_1 : {
                text:[
                    '언제부터 비가 안왔을까?',
                    '피해는 어느정도인지 알아보자.',
                ],
                theme : "T"
            },
            answer_2 : {
                text : [
                    '내 백성의 고통을 몰랐다니..',
                    '정말 미안하다.',
                ],
                theme : "F"
            }
        },
        {
            gaze:7,
            level : 6,
            sentence:[
                '그래! 오래된 지붕을',
