                theme : "N"
            }
        },
        {
            gaze:8,
            level : 7,
            sentence:[
                '다음 마을에서 만난 절친 보리!',
                '함께 놀다보니 이틀이 훌쩍 지났는데..',
            ],
            answer_1 : {
                text:[
                    '오랜만에 실컷 노느라 에너지 다 쏟았네.',
                    '좀 쉬었다가 가야겠다.',
