                theme : "E"
            },
            answer_2 : {
                text : [
                    '역시 놀아야 에너지 충전이지!',
                    '이 기분 그대로 출발하자.',
                ],
                theme : "I"
            }
        },
        {
            gaze:9,
            level : 8,
            sentence:[
                '밤새 내린 비에 지나가는 길이 돌무너기로',
                '막혀있다. 아무도 나서지 않는 상황..',
            ],
            answer_1 : {
                text:[
                    '어떤 돌부터 치워볼까?',
                    '아! 사다리를 찾아보자.',
                ],
                theme : "J"
            },
