  methods:{
    back(){
        if(this.page_idx==0){
            this.$router.back();
        }else{
            this.page_idx--;
            if(this.page_idx==3){
                setTimeout(() => {
                    $('.sub_story_2>.rolling_area>.img').addClass('on');
                }, 500);
            }
            if(this.page_idx==8){
                setTimeout(() => {
                    $('.sub_story_3 .chat_bubble').addClass('on');
                }, 500);
            }
            var page_index = this.page_idx;
            if(page_index==1 || page_index==5 || page_index==6 || page_index==10 || page_index==11 || page_index==13 || page_index==14
            || page_index==15|| page_index==16|| page_index==17|| page_index==18){
                this.qna_list_idx--;
                this.append_theme=this.append_theme.slice(0,this.qna_list_idx);
            }
            if(page_index==0 || page_index==4 || page_index==9 || page_index==12){
                this.story=false;
                this.sub_stroy=true;
            }
            if(page_index==1 || page_index==5 || page_index==6 || page_index==11){
                this.story=true;
                this.sub_stroy=false;
            }
        }
        console.log(this.append_theme);
    },
    next_qna(theme,event){
        var targetId = event.target;
        var tagName = (targetId.tagName);
        var targetTag=$(targetId);
        if(tagName=='P'){
            targetTag = $(targetId).parent('li');
        }
        targetTag.addClass('on');
        this.append_theme.push(theme);
        setTimeout(() => {
            this.page_idx++;
            this.qna_list_idx++;
            console.log(this.page_idx);
            if(this.page_idx==2 || this.page_idx==7 || this.page_idx==12 || this.page_idx==20){
                this.story=false;
                this.sub_stroy=true;
            }
            targetTag.removeClass('on');
        }, 500);

    },
    calc_fun(){
        var res_list = this.append_theme;
        var res_obj={
            e:0,i:0,s:0,t:0,j:0,p:0,n:0,f:0,
        }
        for(var res of res_list){
            switch (res) {
                case 'I':
                    res_obj.i++;
                    break;
                case 'E':
                    res_obj.e++;
                    break;
                case 'S':
                    res_obj.s++;
                    break;
                case 'T':
                    res_obj.t++;
                    break;
                case 'F':
                    res_obj.f++;
                    break;
                case 'P':
                    res_obj.p++;
                    break;
                case 'N':
                    res_obj.n++;
                    break;
                case 'J':
                    res_obj.j++;
                    break;
                default:
                    break;
            }
        }
        var res_txt="";
        if(res_obj.i>res_obj.e){
            res_txt+="I";
        }else if(res_obj.i<res_obj.e){
            res_txt+="E";
        }
        if(res_obj.s>res_obj.n){
            res_txt+="S";
        }else if(res_obj.s<res_obj.n){
            res_txt+="N";
        }
        if(res_obj.t>res_obj.f){
            res_txt+="T";
        }else if(res_obj.t<res_obj.f){
            res_txt+="F";
        }
        if(res_obj.j>res_obj.p){
            res_txt+="J";
        }else if(res_obj.j<res_obj.p){
            res_txt+="P";
        }
        var flow_num=0;
        for(var item of this.mbti_list){
            if(item==res_txt){
                break
            }
            flow_num++;
        }
        this.$router.push('/result?idx='+flow_num);
    },
    goto_story(){
        this.page_idx++;
        if(this.page_idx==3){
            setTimeout(() => {
                $('.sub_story_2>.rolling_area>.img').addClass('on');
            }, 500);
        }
        if(this.page_idx==8){
            setTimeout(() => {
                $('.sub_story_3 .chat_bubble').addClass('on');
            }, 500);
        }
        if(this.page_idx==1 || this.page_idx==5 || this.page_idx==10 || this.page_idx==13){
            this.story=true;
            this.sub_stroy=false;
        }
        if(this.page_idx==22){
            this.calc_fun();
        }
    },
    popup_word(){
        window.open('https://wordsquare.org/', '_blank');
    },
  },
}
